



















































































































import { Vue, Component, Watch, Prop } from "vue-property-decorator";
import ClassifyDevice from "./components/ClassifyDeviceList.vue";
import utils from "@/utils/utils";
import api from "@/microApps/deviceManage/api";

@Component({
  components: { ClassifyDevice }
})
export default class DeviceArchived extends Vue {
  treeData = [];
  // 配置选项
  defaultProps = {
    children: "children",
    label: "name"
  };
  currentClassifyId = "";
  currentClassifyName = "";
  // 选中表单信息
  basicForm: any = {};
  // 弹窗表单信息
  classifyTreeNodeDialog: boolean = false;
  dialogBasicForm: any = {
    id: "",
    parentId: null,
    parentName: "",
    name: "",
    code: "",
    remark: ""
  };
  loading = false;
  addRules = {
    name: { required: true, message: "请输入名称", trigger: "blur" },
    code: { required: true, message: "请输入编码", trigger: "blur" }
  };
  paging = {
    size: 10,
    current: 1,
    total: 0,
    query: {}
  };

  created() {
    this.getClassifyTreeData();
  }

  // 获取树数据
  getClassifyTreeData() {
    this.$http.get(api.classifyTree).then((res: any) => {
      if (res.data.status) {
        this.treeData = res.data.data;
        this.treeData.length && this.handleNodeClick(this.treeData[0]);
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  // 树节点点击事件
  handleNodeClick(data: any) {
    this.basicForm = data;
    this.currentClassifyId = data.id;
    this.currentClassifyName = data.name;
  }

  //保存设备归类信息
  saveClassify() {
    let refs: any = this.$refs.dialogBasicForm;
    refs.validate((valid: boolean) => {
      if (valid) {
        this.loading = true;
        let url = this.dialogBasicForm.id ? api.updateClassify : api.addClassify;
        this.$http
          .post(url, this.dialogBasicForm)
          .then((res: any) => {
            this.loading = false;
            if (res.data.status) {
              this.$message.success("操作成功");
              this.classifyTreeNodeDialog = false;
              this.getClassifyTreeData();
            } else {
              this.$message({
                message: res.data.message,
                type: "error"
              });
            }
          })
          .catch(() => {
            this.loading = false;
          });
      }
    });
  }

  // 打开节点弹窗
  openClassifyTreeNodeForm(buttonName: any, data: any) {
    this.classifyTreeNodeDialog = true;
    this.$nextTick(() => {
      let refs: any = this.$refs.dialogBasicForm;
      refs.resetFields();
      switch (buttonName) {
        case "add":
          this.dialogBasicForm = {
            id: "",
            parentId: data.id,
            parentName: data.name,
            name: "",
            code: "",
            remark: "",
            level: data.level + 1
          };
          break;
        case "edit":
          utils.mapValue(this.dialogBasicForm, data);
          break;
        case "addTop":
          this.dialogBasicForm = {
            id: "",
            parentId: null,
            parentName: "",
            name: "",
            code: "",
            remark: "",
            level: 1
          };
          break;
      }
    });
  }

  // 删除节点
  remove(node: any, data: any) {
    this.$confirm("确认删除?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {
        this.$http.get(`${api.deleteClassify}${data.id}`).then((res: any) => {
          if (res.status) {
            if (res.data.status) {
              this.$message.success("操作成功");
              this.getClassifyTreeData();
            } else {
              this.$message({
                showClose: true,
                message: res.data.message,
                type: "error"
              });
            }
          }
        });
      })
      .catch(() => {});
  }
}
