import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import DeviceArchived from "@/microApps/deviceManage/views/deviceArchived/DeviceArchived.vue";
import DeviceDetail from "@/microApps/deviceManage/views/deviceManage/DeviceDetail.vue";

@Component({
  components: {
    DeviceArchived,
    DeviceDetail
  }
})
export default class DeviceArchivedIndex extends Vue {
  whichComponent: string = "DeviceArchived";
  deviceId: string = "";
  switchComponent(buttonName: String, row: any) {
    switch (buttonName) {
      case "DeviceDetail":
        this.whichComponent = "DeviceDetail";
        this.deviceId = row.deviceId;
        break;
      default:
        this.whichComponent = "DeviceArchived";
        this.deviceId = "";
    }
  }
}
