







































































import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import api from "@/microApps/deviceManage/api";

@Component({
  components: {}
})
export default class ClassifyDeviceList extends Vue {
  @Prop({ type: [String, Number], default: "" })
  currentId!: string;
  @Prop({ type: Number })
  deviceTableDataTotal!: any;
  @Prop({ type: Object })
  paging!: any;
  loading = false;
  allDeviceTableData: any = [];
  addDeviceDialog: any = false;
  deviceData: Array<any> = [];
  dialogPaging = {
    size: 10,
    current: 1,
    total: 0,
    query: {
      name: "",
      code: ""
    }
  };

  created() {
    this.currentId && this.getClassifyDeviceList();
  }

  // 获取归类设备列表
  getClassifyDeviceList() {
    this.$http.post(api.getClaasifyDeviceList + this.currentId, this.paging).then((res: any) => {
      if (res.data.status) {
        this.deviceData = res.data.data.records;
        this.paging.total = res.data.data.total;
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  // 获取未归类设备列表
  listNotBindByClassifyId() {
    this.loading = true;
    this.$http
      .post(api.listNotBindByClassifyId + this.currentId, this.dialogPaging)
      .then((res: any) => {
        this.loading = false;
        if (res.data.status) {
          this.allDeviceTableData = res.data.data.records;
          this.dialogPaging.total = res.data.data.total;
        } else {
          this.$message.error(res.data.message);
        }
      })
      .catch(() => {
        this.loading = false;
      });
  }

  // 点击添加设备
  handleAddFunc() {
    this.listNotBindByClassifyId();
    this.addDeviceDialog = true;
  }

  // 添加设备
  handleDeviceChoose(row: any, index: number) {
    this.$http
      .post(api.classifyBind, {
        deviceClassifyId: this.currentId,
        deviceId: row.id
      })
      .then((res: any) => {
        if (res.data.status) {
          this.$message.success("添加成功");
          this.listNotBindByClassifyId();
          this.getClassifyDeviceList();
        } else {
          this.$message.error(res.data.message);
        }
      });
  }

  // 删除设备
  handleDelFunc(row: any) {
    this.$confirm("确认删除?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    }).then(() => {
      this.$http.get(api.classifyUnbind + row.id).then((res: any) => {
        if (res.data.status) {
          this.$message.success("删除成功");
          this.getClassifyDeviceList();
        } else {
          this.$message.error(res.data.message);
        }
      });
    });
  }
}
